    outline: none;
    text-decoration: none; } }

p {
  padding: 0;
  margin: 0 0 30px 0; }

h1, h2, h3 {
  font-family: 'Michroma', sans-serif;
  font-weight: 400;
  margin: 0 0 20px 0;
