/* Prelaoder */

#preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #fff url("../../img/preloader.svg") no-repeat center center; }

/* Back to top button */

.back-to-top {
  position: fixed;
  display: none;
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
  padding: 6px 12px 9px 12px;
  font-size: 16px;
  border-radius: 2px;
  right: 15px;
  bottom: 15px;
  transition: background 0.5s;
  &:focus {
    background: rgba(0, 0, 0, 0.2);
    color: #fff;
    outline: none; }
  &:hover {
    background: #2dc997;
    color: #fff; } }

@media (max-width: 768px) {
  .back-to-top {
    bottom: 15px; } }

